import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { HttpClient } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { AdmLogs } from '../models/logs';

@Injectable({
  providedIn: 'root'
})
export class HomeService {
  
  constructor(private http: HttpClient) { 

  }

  totalUsuarios() {
    return this.http.get<any>(`${environment.url_api}usuarios/count/totales`).pipe(
        map(data => {  
            return data;
        })
    );
  }

  addLogLogin(datos: AdmLogs) {
    let data = {
      nombre_tabla: datos.nombre_tabla,
      nombre_objeto: datos.nombre_objeto,
      id_objeto: datos.id_objeto,
      tipo_transaccion: datos.tipo_transaccion,
      descripcion: datos.descripcion,
      nombre: datos.nombre,
      usuario: datos.usuario,
      publicIP: datos.publicIP,
      privateIP: datos.privateIP,
      navegator: datos.navegator,
      fecha_creacion: datos.fecha_creacion
    }

    console.log(data);

    return this.http.post<any>(`${environment.url_api}addLog`, data).pipe(
      map(result => {      
        return result.Usuario;
      })
    );  
  }
}
