import { Component, OnInit } from '@angular/core';

import { NzModalService } from 'ng-zorro-antd';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import { NzFormatEmitEvent } from 'ng-zorro-antd';
import { NgxPermissionsService } from 'ngx-permissions';
import { UserService } from '../../services/user.service';
import { RolesService } from '../../services/roles.service';
import { User } from '../../models/user';
import { Roles } from 'src/app/models/role';
import { BehaviorSubject, Observable } from 'rxjs';
import { HttpClient } from '@angular/common/http';
import { AdmLogs } from 'src/app/models/logs';
import { HomeService } from 'src/app/services/home.service';
import { DatePipe } from '@angular/common';

@Component({
    selector: 'app-users',
    templateUrl: './users.component.html',
    styleUrls: ['./users.component.css']
})

export class UsersComponent implements OnInit {
    private currentUserSubject: BehaviorSubject<User>
    public currentUser: Observable<User>;

    nzEvent(event: NzFormatEmitEvent): void {
        console.log(event);
    }

    isLoading: boolean = true;

    searchValue = '';

    public users: User[] = [];
    public user: User;

    public userDataSelected: User;

    passwordVisible = false;
    password: string;

    roles: Roles;

    rolesPermisos: Roles;

    userAgregarForm: FormGroup;

    submittedAgregarUsuario = false;

    isVisibleAgregarUsuario = false;
    isConfirmLoadingAgregarUsuario = false;

    isVisibleRoles = false;
    isConfirmLoadingRoles = false;

    userEditarForm: FormGroup;

    submittedEditarUsuario = false;

    isVisibleEditarUsuario = false;
    isConfirmLoadingEditarUsuario = false;
    now = new Date();

    ipAddressPublic: any; 
    ipAddressPrivate: any; 
    
    public data = [];
    public dateLogin;
  
    browserName = '';
    browserVersion = '';

    constructor(
        private permissionsService: NgxPermissionsService,
        private modalService: NzModalService,
        private rolesServices: RolesService,
        private homeServices: HomeService,
        private userService: UserService,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private datepipe: DatePipe,
        private http: HttpClient,
    ) {
        this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('currentUser')));
        this.currentUser = this.currentUserSubject.asObservable();

        this.userService.getUsers().subscribe(request => {
            this.users = request;
            this.isLoading = false;
        },
            error => {
                console.log(error);
            }
        );

        this.rolesServices.getRoles().subscribe(roles => {
            this.roles = roles;            
        }, err => {
            console.log(err);
        });

    }

    ngOnInit() {
        this.dateLogin = this.datepipe.transform(this.now, 'dd/MM/yyyy hh:mm:ss', 'es');

        this.userAgregarForm = this.formBuilder.group({
            nombre_empleado: ['', Validators.required],
            nombre_usuario: ['', Validators.required],
            contrasena: ['', Validators.required],
            rol: ['', Validators.required],
            cargo: ['', Validators.required]
        });

        this.userEditarForm = this.formBuilder.group({
            nombre_empleado: ['', Validators.required],
            nombre_usuario: ['', Validators.required],
            contrasena: [''],
            rol: ['', Validators.required],
            cargo: ['', Validators.required]
        });

        this.getIPAddress();

        this.browserName = this.detectBrowserName();
        this.browserVersion = this.detectBrowserVersion();
    
        setTimeout(() => {      
          this.Logs('usuarios', 'Consulto modulo usuarios', this.currentUserSubject.value.id_usuario, 'Consulta', 'Consulto el modulo de usuarios: ');
        }, 4000);
    }

    getIPAddress() {  
        this.http.get<{ip:string}>('https://jsonip.com').subscribe( data => {
          this.ipAddressPrivate = data['ip'];   
        });
    
        this.http.get<{ip:string}>('https://api.ipify.org?format=json').subscribe( data => {
          this.ipAddressPublic = data['ip'];
        });
    } 

    detectBrowserName() { 
    const agent = window.navigator.userAgent.toLowerCase()
    switch (true) {
        case agent.indexOf('edge') > -1:
        return 'edge';
        case agent.indexOf('opr') > -1 && !!(<any>window).opr:
        return 'opera';
        case agent.indexOf('chrome') > -1 && !!(<any>window).chrome:
        return 'chrome';
        case agent.indexOf('trident') > -1:
        return 'ie';
        case agent.indexOf('firefox') > -1:
        return 'firefox';
        case agent.indexOf('safari') > -1:
        return 'safari';
        default:
        return 'other';
    }
    }
    
    detectBrowserVersion(){
        var userAgent = navigator.userAgent, tem, 
        matchTest = userAgent.match(/(opera|chrome|safari|firefox|msie|trident(?=\/))\/?\s*(\d+)/i) || [];
        
        if(/trident/i.test(matchTest[1])){
            tem =  /\brv[ :]+(\d+)/g.exec(userAgent) || [];
            return 'IE '+(tem[1] || '');
        }

        if(matchTest[1]=== 'Chrome'){
            tem = userAgent.match(/\b(OPR|Edge)\/(\d+)/);
            if(tem!= null) return tem.slice(1).join(' ').replace('OPR', 'Opera');
        }

        matchTest= matchTest[2]? [matchTest[1], matchTest[2]]: [navigator.appName, navigator.appVersion, '-?'];

        if((tem= userAgent.match(/version\/(\d+)/i))!= null) matchTest.splice(1, 1, tem[1]);
        return matchTest.join(' ');
    }

    Logs(tabla: string, objeto: string, id: string, tipo: string, descipcion: string) {
    var navegadorUsed = this.browserName + ', ' + this.browserVersion; 
    var descripcion = descipcion + ' por ' + this.currentUserSubject.value.nombre_usuario;

    if(this.ipAddressPrivate == undefined) {
        this.ipAddressPrivate = '';
    }
    if(this.ipAddressPublic == undefined) {
        this.ipAddressPublic = '';
    }
    
    let metaData = new AdmLogs(
        tabla,
        objeto,
        id,
        tipo,
        descripcion,
        this.currentUserSubject.value.nombre_empleado,
        this.currentUserSubject.value.nombre_usuario,
        this.ipAddressPublic,
        this.ipAddressPrivate,
        navegadorUsed,
        this.dateLogin
    );
    this.homeServices.addLogLogin(metaData).subscribe(result => {

    }, err => {
        console.log(err);
    }); 
    }

    get fau() { return this.userAgregarForm.controls; }
    get feu() { return this.userEditarForm.controls; }

    showModalAgregarUsuario(): void {
        setTimeout(() => {      
            this.Logs('usuarios', 'Consulta la actividad agregar usuario' + 'null', 'null', 'Consulta', 'Visualizo los detalles de creacion de usuario ');
          }, 1000);
        this.isVisibleAgregarUsuario = true;

        this.userAgregarForm.get('nombre_empleado').setValue(null);
        this.userAgregarForm.get('nombre_usuario').setValue(null);
        this.userAgregarForm.get('rol').setValue(null);
        this.userAgregarForm.get('cargo').setValue(null);
        this.password = null;
    }

    handleCancelAgregarUsuario(): void {
        this.isVisibleAgregarUsuario = false;
        this.submittedAgregarUsuario = false;
    }

    onSubmitAgregarUsuario() {
        this.submittedAgregarUsuario = true;

        if (this.userAgregarForm.invalid) {
            return;
        }

        this.isConfirmLoadingAgregarUsuario = true;

        let user = new User(
            null,
            this.userAgregarForm.get('nombre_empleado').value,
            this.userAgregarForm.get('nombre_usuario').value,
            this.userAgregarForm.get('contrasena').value,
            this.userAgregarForm.get('rol').value,
            null,
            this.userAgregarForm.get('cargo').value
        );
        
        this.userService.addUser(user).subscribe(result => {
            console.log(result);

            let role = Object.values(this.roles).find(value =>{
                return value.id_role == result.id_role
            });

            this.users.push(new User(
                result.id_usuario,
                result.nombre_empleado,
                result.nombre_usuario,
                result.contrasena,
                null,
                null,
                null,
                null,
                null,
                null,
                null,
                role.nombre_role,
                result.cargo
            ));

            this.users = [...this.users];

            this.isConfirmLoadingAgregarUsuario = false;
            this.isVisibleAgregarUsuario = false;
            this.toastr.success('Usuario Agregado!');

            setTimeout(() => {      
                this.Logs('usuarios', 'Creacion de un nuevo usuario: ' + result.nombre_usuario, result.nombre_empleado, 'Creacion', 'Creacion de un nuevo usuario: ' + this.userAgregarForm.get('nombre_usuario').value + ' ');
            }, 1000);

        }, err => {
            this.isConfirmLoadingAgregarUsuario = false;

            if (err.error.msg.error.fields) {
                if (err.error.msg.error.fields.nombre_usuario)
                    this.toastr.error('El nombre de usuario ingresado ya existe.');
            } else {
                this.toastr.error('Hubo un error al agregar el usuario');
            }
        })
        this.submittedAgregarUsuario = false;
    }

    showModalEditarUsuario(data: string): void {
        this.userDataSelected = JSON.parse(JSON.stringify(data));
        setTimeout(() => {      
            this.Logs('usuarios', 'Consulta actividad de edicion usuario' + this.userDataSelected.nombre_usuario, this.userDataSelected.nombre_empleado, 'Consulta', 'Visualizo los detalles de edicion de usuario ');
          }, 1000);

        this.userEditarForm.controls['nombre_empleado'].setValue(this.userDataSelected.nombre_empleado);
        this.userEditarForm.controls['nombre_usuario'].setValue(this.userDataSelected.nombre_usuario);
        this.userEditarForm.controls['contrasena'].setValue(this.userDataSelected.contrasena);
        this.userEditarForm.controls['rol'].setValue(String(this.userDataSelected.id_role));
        this.userEditarForm.controls['cargo'].setValue(String(this.userDataSelected.cargo));
        this.isVisibleEditarUsuario = true;
    }

    handleCancelEditarUsuario(): void {
        this.isVisibleEditarUsuario = false;
        this.submittedEditarUsuario = false;
    }

    onSubmitEditarUsuario() {
        this.submittedEditarUsuario = true;

        if (this.userEditarForm.invalid) {
            return;
        }

        this.isConfirmLoadingEditarUsuario = true;

        let user = new User(
            this.userDataSelected.id_usuario,
            this.userEditarForm.get('nombre_empleado').value,
            this.userEditarForm.get('nombre_usuario').value,
            this.userEditarForm.get('contrasena').value,
            this.userEditarForm.get('rol').value,
            this.userEditarForm.get('cargo').value
        );

        this.userService.updateUsers(user)
            .subscribe(result => {
                this.isConfirmLoadingEditarUsuario = false;
                this.isVisibleEditarUsuario = false;
                this.toastr.success('Usuario actualizado!');

                setTimeout(() => {      
                    this.Logs(
                      'usuarios', 
                      'Edicion del usuario ' + this.userDataSelected.nombre_usuario, 
                      this.userDataSelected.nombre_empleado, 
                      'Actualizacion', 
                      'Realizo cambios en el campo de nombre_empleado de "' + this.userDataSelected.nombre_empleado + '" a "' + this.userEditarForm.get('nombre_empleado').value + 
                      '" y realizo cambios en nombre_usuario de "' + this.userDataSelected.nombre_usuario + '" a "' + this.userEditarForm.get('nombre_usuario').value + '"' +
                      '" y realizo cambios en contrasena de "' + this.userDataSelected.contrasena + '" a "' + this.userEditarForm.get('contrasena').value + '"' +
                      '" y realizo cambios en rol de "' + this.userDataSelected.id_role + '" a "' + this.userEditarForm.get('rol').value + '"' +
                      '" y realizo cambios en cargo de "' + this.userDataSelected.cargo + '" a "' + this.userEditarForm.get('cargo').value + '".'
                    );
                  }, 1000);

                this.userService.getUsers().subscribe(request => {
                        this.users = request;
                        this.isLoading = false;
                    },
                        error => {
                            console.log(error);
                        }
                    );

            }, err => {
                console.log(err);
                this.isConfirmLoadingEditarUsuario = false;

                if (err.error.msg.error.fields) {
                    if (err.error.msg.error.fields.nombre_usuario)
                        this.toastr.error('El nombre de usuario ingresado ya existe.');
                } else {
                    this.toastr.error('Hubo un error al editar el usuario');
                }
            })

        this.submittedEditarUsuario = false;
    }

    showDeleteConfirm(data: string): void {
        this.userDataSelected = JSON.parse(JSON.stringify(data));
        this.modalService.confirm({
            nzTitle: '¿Esta seguro que desea eliminar el usuario?',
            nzContent: '<b style="color: red;">Se eliminara el usuario: ' + this.userDataSelected.nombre_empleado + '</b>',
            nzOkText: 'Si',
            nzOkType: 'danger',
            nzOnOk: () => {
                this.userService.deleteUser(this.userDataSelected.id_usuario).subscribe(result => {
                        this.toastr.success('Usuario eliminado correctamente!');

                        setTimeout(() => {      
                            this.Logs(
                              'usuarios', 
                              'Eliminacion del usuario ' + this.userDataSelected.nombre_usuario, 
                              this.userDataSelected.nombre_empleado, 
                              'Eliminacion', 
                              'Se elimino el usuario: ' + this.userDataSelected.nombre_usuario + ' '
                            );
                          }, 1000);

                        this.users = this.users.filter(obj => obj.id_usuario !== this.userDataSelected.id_usuario);
                        this.users = [...this.users];
                    }, error => {
                        this.toastr.error('Hubo un error al eliminar el usuario');
                    });
            },
            nzCancelText: 'No',
            nzOnCancel: () => {
            }
        });
    }
}
